import React from 'react'
import {
    Button,
    Cell,
    Div,
    Group,
    HeaderButton,
    Input,
    List,
    Panel,
    PanelHeader,
    View
} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'
import SweetSelect from './SweetSelect'
import BackButton from './BackButton'
import './Home.css'
import Icon24VKLogo from '@vkontakte/icons/dist/24/logo_vk'

class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = this.getInitialState()
    }

    changePanel(id) {
        this.setState({activePanel: id})
    }

    goHome() {
        this.changePanel('main')
    }

    render() {
        return (
            <View
                id={this.props.id}
                header={true}
                activePanel={this.state.activePanel}
            >
                <Panel id="main">
                    <PanelHeader>Расчет ТО</PanelHeader>
                    <Group title="Рассчитайте стоимость технического обслуживания машины:">
                        <List>
                            <Cell
                                indicator={this.state.model.text}
                                onClick={() => this.changePanel('chooseModel')}
                            >
                                Модель авто
                            </Cell>
                            <Cell
                                indicator={this.state.modification.text}
                                onClick={() =>
                                    this.changePanel('chooseModification')
                                }
                            >
                                Модификация
                            </Cell>
                            <Cell
                                indicator={this.state.oldness.text}
                                onClick={() =>
                                    this.changePanel('chooseOldness')
                                }
                            >
                                Пробег или время
                            </Cell>
                        </List>
                    </Group>
                    <Group title={'Работы'}>
                        <Cell indicator={'2000 руб'} className={'info'}>
                            Покраска
                        </Cell>
                        <Cell indicator={'2000 руб'} className={'info summary'}>
                            Всего по работам
                        </Cell>
                    </Group>
                    <Group title={'материалы'}>
                        <Cell indicator={'5000 руб'} className={'info'}>
                            Краска
                        </Cell>
                        <Cell indicator={'5000 руб'} className={'info summary'}>
                            Всего по материалам
                        </Cell>
                    </Group>
                    <Group>
                        <Cell indicator={'7000 руб'} className={'info summary'}>
                            Итого
                        </Cell>
                    </Group>
                    <Div>
                        <p>
                            Отправь заявку, мы свяжемся с тобой за час и
                            подскажем, как привести твою ласточку в идеальное
                            состояние :)
                        </p>
                        <Button
                            align="center"
                            stretched
                            size="l"
                            onClick={() => this.changePanel('sendRequest')}
                        >
                            Отправить заявку
                        </Button>
                    </Div>
                </Panel>
                <SweetSelect
                    id="chooseModel"
                    backClickHandler={() => this.goHome()}
                    header="Выбери модель"
                    items={[{id: 1, value: 'Almera Classic (2006-2013)'}]}
                    onSelect={(item) => this.setSelectValue('model', item)}
                />
                <SweetSelect
                    id="chooseModification"
                    backClickHandler={() => this.goHome()}
                    header="Выбери модификацию"
                    items={[{id: 1, value: 'лучшая'}]}
                    onSelect={(item) =>
                        this.setSelectValue('modification', item)
                    }
                />
                <SweetSelect
                    id="chooseOldness"
                    backClickHandler={() => this.goHome()}
                    header="Выбери пробег или время"
                    items={[{id: 1, value: '15000 или 1 год'}]}
                    onSelect={(item) => this.setSelectValue('oldness', item)}
                />
                <Panel id={'sendRequest'}>
                    <PanelHeader
                        left={
                            <HeaderButton onClick={() => this.goHome()}>
                                <BackButton />
                            </HeaderButton>
                        }
                    >
                        Отправить заявку
                    </PanelHeader>
                    <Group>
                        <Div
                            style={{
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center'
                            }}
                        >
                            <Button
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center'
                                }}
                            >
                                <div>
                                    <span
                                        style={{
                                            display: 'flex',
                                            alignItems: 'center'
                                        }}
                                    >
                                        <Icon24VKLogo />
                                        <span>
                                            Отправить заявку с данными профиля
                                        </span>
                                    </span>
                                </div>
                            </Button>
                            <Div
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center'
                                }}
                            >
                                <span>или</span>
                            </Div>
                        </Div>
                        <div>
                            <Cell>
                                <span>Имя</span>
                                <Input type={'text'} placeholder={'Иван'}/>
                            </Cell>
                            <Cell>
                                <span>Телефон</span>
                                <Input
                                    type={'tel'}
                                    placeholder={'+79211234567'}
                                />
                            </Cell>
                            <Cell>
                                <Button stretched size={'l'}>Отправить заявку</Button>
                            </Cell>
                        </div>
                    </Group>
                </Panel>
            </View>
        )
    }

    setSelectValue(key, item) {
        this.setState({
            [key]: this.convertFromSweetSelectToHomeItemsFormat(item)
        })
    }

    getInitialState() {
        const state = {}
        state.activePanel = 'main'
        Object.assign(state, this.getDefaultSelectState('Выбрать'))
        return state
    }

    convertFromSweetSelectToHomeItemsFormat(item) {
        return {
            id: item.id,
            text: item.value
        }
    }

    getDefaultSelectState(text) {
        return {
            model: {id: 0, text},
            modification: {id: 0, text},
            oldness: {id: 0, text}
        }
    }
}

export default Home



